import React, { Component } from 'react';

import { ContactContainer, TextArea, FormArea } from './styles';

import bar from '../../images/barra-spin.png';

export default class Contact extends Component {
  constructor() {
    super();

    this.state = {
      email: '',
      name: '',
      company: '',
      website: '',
      message: '',
      rentability: '',
    };
  }

  handleChange = e => {
    switch (e.target.name) {
      case 'name':
        this.setState({ name: e.target.value });
        break;
      case 'email':
        this.setState({ email: e.target.value });
        break;
      case 'website':
        this.setState({ website: e.target.value });
        break;
      case 'message':
        this.setState({ message: e.target.value });
        break;
      case 'company':
        this.setState({ company: e.target.value });
        break;
      default:
        this.setState({ rentability: e.target.value });
        break;
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    console.log(this.state);
  };

  render() {
    const {
      value: email,
      value: name,
      value: company,
      value: website,
      value: message,
      value: rentability,
    } = this.state;

    return (
      <ContactContainer className="unhideMobile" id="cForm">
        <TextArea>
          <img src={bar} alt="Spin Bar" className="spinBar"/>
          <div>
            <h1>Entre em contato</h1>
            <p>
              Spin Pay é o primeiro gateway de pagamentos instantâneos do Brasil
              que conecta pagadores e recebedores, criada para ser rápida e
              segura.
            </p>
          </div>
        </TextArea>
        <FormArea>
          <form onSubmit={this.handleSubmit}>
            <input className="inputform"
                type="text"
                placeholder="Seu nome"
                name="name"
                value={name}
                onChange={this.handleChange}
              />
              <input className="inputform"
                type="text"
                placeholder="Email"
                name="email"
                value={email}
                onChange={this.handleChange}
              />

              <input className="inputform"
                type="text"
                placeholder="Nome da empresa"
                name="companyName"
                value={company}
                onChange={this.handleChange}
              />
              <input className="inputform"
                type="text"
                placeholder="Site"
                name="website"
                value={website}
                onChange={this.handleChange}
              />

              <select className="selectform" value={rentability} onChange={this.handleChange}>
              <option value="" disabled selected>Faturamento anual</option>
             <option value="Até R$: 200 mil">
               Até R$: 200 mil
             </option>
               <option value="Entre R$: 200 mil até R$: 1 milhão">
                 Entre R$: 200 mil até R$: 1 milhão
               </option>
               <option value="Entre R$: 1 milhão até R$: 2,5 milhões">
                 Entre R$: 1 milhão até R$: 2,5 milhões
               </option>
               <option value="Entre R$: 2,5 milhões até R$: 10 milhões">
                 Entre R$: 2,5 milhões até R$: 10 milhões
               </option>
               <option value="Entre R$: 10 milhões até R$: 100 milhões">
                 Entre R$: 10 milhões até R$: 100 milhões
               </option>
               <option value="Entre R$: 100 milhões até R$: 300 milhões">
                 Entre R$: 100 milhões até R$: 300 milhões
               </option>
              </select>

            <h3>Mensagem</h3>

              <textarea className="messagefield"
                type="text"
                name="message"
                value={message}
                onChange={this.handleChange}
              />

            <span className="sendContactBtn">
              <input type="submit" value="Enviar" />
            </span>
          </form>
        </FormArea>
      </ContactContainer>
    );
  }
}
