import React, { Component } from 'react';
import Switch from 'react-switch';
import ReactPlayer from 'react-player';

import { TechSection, ToggleDiv, WebMobileDiv } from './styles';

import laptop from '../../images/comp.png';
import phone from '../../images/phone.png';
import webVideo from '../../images/web_video.mp4';
import mobVideo from '../../images/mobile_video.mp4';
import mobVideop from '../../images/videos/Mobile.webm';
import webVideop from '../../images/videos/Laptop.webm';
import bar from '../../images/barra-spin.png';

export default class Technology extends Component {
  constructor() {
    super();
    this.state = { check: false };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(checked) {
    this.setState({ check: checked });
  }

  render() {
    const { check } = this.state;
    return (
      <TechSection id="technology">
        <WebMobileDiv className="spindesktop">
          <div className="imagesDiv">
            {check ? (
              <ReactPlayer className="mobilevideo" url={mobVideop} playing loop />
            ) : (
              <ReactPlayer className="desktopvideo" url={webVideop} playing loop />
            )}
          </div>
          <div className="sliderText">
            <img src={bar} alt="Barra Spin" className="spinBar" />
            <div className="descricaotech">
            <p>
            Spin Pay é o primeiro gateway de pagamentos instantâneos do
            Brasil, que conecta pagadores e recebedores, criado para ser
            rápido e seguro.
            </p>
            </div>
          </div>
        </WebMobileDiv>
        <ToggleDiv className="spindesktop">
          <p>Fluxo de pagamento web</p>
          <Switch
            onChange={this.handleChange}
            checked={check}
            className="react-switch"
            uncheckedIcon={false}
            checkedIcon={false}
          />
          <p>Fluxo de pagamento mobile</p>
        </ToggleDiv>

        <div className="spinmobile" id="technology">
          <div>
            <div className="description">
              <img src={bar} alt="Barra Spin" className="spinBar" />
              <h2 className="whiteheading webtitle">Web</h2>
              <p className="whitep descriptionweb">
              Spin Pay é o primeiro gateway de pagamentos instantâneos do
              Brasil, que conecta pagadores e recebedores, criado para ser
              rápido e seguro.
              </p>
            </div>
            <div className="imagesDiv">
              <img src={laptop} alt="Laptop" className="imageSlider laptop" />
            </div>
            <div className="imagesDiv">
              <img src={phone} alt="Pessoa" className="imageSlider phone" />
            </div>
            <div className="sliderText">
              <h2 className="whiteheading mobtitle">Mobile</h2>
              <p className="whitep descriptionmob">
                Spin Pay é o primeiro gateway de pagamentos instantâneos do
                Brasil, que conecta pagadores e recebedores, criado para ser
                rápido e seguro.
              </p>
            </div>
          </div>
        </div>
      </TechSection>
    );
  }
}
