import React from 'react';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import { useSpring, animated } from 'react-spring';

import bar from '../../../images/barra-spin.png';
import { useStyles } from './styles';

import ContactFormModular from '../contactForm';

const Fade = React.forwardRef(function Fade(props, ref) {
  const { in: open, children, onEnter, onExited, ...other } = props;
  const style = useSpring({
    from: { opacity: 0 },
    to: { opacity: open ? 1 : 0 },
    onStart: () => {
      if (open && onEnter) {
        onEnter();
      }
    },
    onRest: () => {
      if (!open && onExited) {
        onExited();
      }
    },
  });

  return (
    <animated.div ref={ref} style={style} {...other}>
      {children}
    </animated.div>
  );
});

export default function SpringModal() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      <span
        onClick={handleOpen}
        role="button"
        onKeyPress={handleOpen}
        tabIndex="0"
        className={classes.makeCursor}
      >
        Contato
      </span>
      <Modal
        aria-labelledby="spring-modal-title"
        aria-describedby="spring-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <div className={classes.intro}>
              <img src={bar} alt="Spin Bar" />
              <h1 id="h1form" className={classes.title}>Entre em contato</h1>
              <p id="pform" className={classes.body}>
                Preencha o formulário de contato e logo retornaremos.
              </p>
            </div>
            <div className={classes.form}>
              <ContactFormModular />
            </div>
          </div>
        </Fade>
      </Modal>
    </>
  );
}
